export default{
	data(){
		return{
			nameValue:null,
			selfDescriptionValue: null,
			phoneValue: null,
			emailValue:null,
			sexValue:null,
			majorValue:null,
			degreeValue:null,
			start_endTimeValue1:null,
			certificatesValue:null,
			skillValue:null,
			projectValue:null,
			positionValue1:null,
			start_endTimeValue2:null,
			contentValue1:null,
			activityValue:null,
			positionValue2:null,
			start_endTimeValue3:null,
			contentValue2:null,
			
			resId:null,
			resume:{},
			avatarUrl: null,
			nickName: null,
	    }
	},
	onLoad:function(e){
		wx.cloud.init()
		const db = wx.cloud.database();
		const _ = db.command;
		this.resId=e.id;
		
		//获取数据
	    wx.cloud.callFunction({
	    	name:'myResume',
	    	data: {
	    		id: this.resId,
	    	}
	    }).then(res => {
	    	this.resume = res.result.data
			console.log(this.resume)
	    }).catch(err => {
	    	console.error(err)
	    })
	},
	
	methods:{
		edit:function(e){
			
			var aData = new Date()
			wx.cloud.init()
			const db = wx.cloud.database();
			wx.cloud.callFunction({
				name:'myResume',
				data:{
						name: this.nickName,
						selfDescription: this.selfDescriptionValue,
						phone: this.phoneValue,
						email:this.emailValue,
						sex:this.sexValue,
						eduBackground:[this.majorValue,this.degreeValue,this.start_endTimeValue1],
						certificates:this.certificatesValue,
						skills:this.skillValue,
						experirnces:[this.projectValue,this.positionValue1,this.start_endTimeValue2,this.contentValue1],
					    activities:[this.activityValue,this.positionValue2,this.start_endTimeValue3,this.contentValue2]
				}
			}).then(res => {
				wx.showToast({
					title: '修改成功',
				})
				setTimeout(
					function(){
						var pages = getCurrentPages();
						var beforePage = pages[pages.length - 2]; 
						uni.navigateBack({
							success: function(){
								console.log(beforePage);
								beforePage.onLoad()
							}
						})
					}
				)
			}).catch(err => {
				console.error(err) 
			})
		}
	}
}
